import React from 'react'

export default class Projects extends React.Component {


  state={
    projects:[]
  }

  


  componentDidMount(){
    fetch("https://jcodes-backend.herokuapp.com/Projects")
    .then(response=>response.json())
    .then(data=> {
      console.log(data);
      this.setState(prevState=>{
        return {
          projects: data
        }
      })

      // console.log(this.state.projects[0].Screenshot[0].url)
  
    })
  }

  

  render(){

    return (
        <div id="portfolio" className="portfolio-area section-padding">
        <div className="container">
          <div className="row">
            <div className="section-title">
              <h2>Everything <span>I've Developed</span></h2>
            </div>
          </div>
        </div>
        <div className="portfolio-content">
          <div className="port-nav">
            <div className="container">
              <div className="row">
                <div className="col-xs-12">
                  {/* <ul className="port-nav-list">
                    <li><a className="img-filter active" data-filter="*">All</a></li>
                    <li><a className="img-filter" data-filter=".fetch">Fetch API</a></li>
                    <li><a className="img-filter" data-filter=".node">Node.js</a></li>
                  </ul> */}
                </div>
              </div>
            </div>
          </div>
          <div className="container">



            <div className="wrapper">

                {

                      this.state.projects.map(item=>
                      
                          
                          <div className={"project-item " + item.skill}>
                            <div className="project-item">
                              <a target="_blank" href={item.Link}>
                                <img src={item.Screenshot[0].url} alt="portfolio image" />
                                
                              </a><div className><a target="_blank" href={item.Link}>
                                </a><div className="overlay-inner"><a target="_blank" href={item.Link}>
                                  </a><h4><a target="_blank" href={item.Link}> </a><a target="_blank" href={item.Link}>{item.Name}</a></h4>
                                </div>
                              </div>
                              <p><a target="_blank" href={item.Link}>View App</a></p>
                            </div>
                          </div>

                      
                      )
                }


            </div>
              
             





            

          </div>
        </div>
      </div>
    )

  }
}
